* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

a {
  color: #333;
  font-size: 2rem;
  font-weight: 500;
  text-decoration: none;
}

a:hover {
  color: #888;
}

@media (max-width: 720px) {
  a {
    font-size: 1.5rem;
  }
}

@media (max-width: 500px) {
  a {
    font-size: 1.1rem;
  }
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: 0 0 12px #0003;
}

img:hover {
  opacity: .7;
  transform: scale(1.03);
}

.header__search-btn {
  color: #eee;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 2rem;
  padding: .5rem 1.5rem;
  font-size: 1.5rem;
}

.header__search-btn:hover {
  color: #333;
  background-color: #eee;
}

@media (max-width: 720px) {
  .header__search-btn {
    font-size: 1rem;
  }
}

@media (max-width: 400px) {
  .header__search-btn {
    font-size: .7rem;
  }
}

.header__search-form {
  width: 80vw;
}

.search-form__search-input {
  width: 100%;
  background-color: #ddd;
  border: 1px solid #ccc;
  border-radius: 50px;
  padding: .5rem 2rem;
  font-size: 1.5rem;
}

.search-form__search-input:hover {
  background-color: #eee;
}

@media (max-width: 720px) {
  .search-form__search-input {
    padding: .5rem 1rem;
    font-size: 1rem;
  }
}

@media (max-width: 400px) {
  .search-form__search-input {
    padding: .5rem 1rem;
    font-size: .7rem;
  }
}

.gallery {
  width: 90%;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem 1rem;
  margin: 2rem auto 4rem;
  display: grid;
}

@media (max-width: 1060px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

header {
  justify-content: space-around;
  align-items: center;
  gap: .5rem;
  margin: 1rem;
  display: flex;
}

