.header__search-btn {
  padding: 0.5rem 1.5rem;
  border: none;
  border-radius: 2rem;
  background-color: $color-dark-gray;
  font-size: 1.5rem;
  color: $color-white;
  cursor: pointer;

  &:hover {
    background-color: $color-white;
    color: $color-dark-gray;
  }
}

@media (max-width: 720px) {
  .header__search-btn {
    font-size: 1rem;
  }
}

@media (max-width: 400px) {
  .header__search-btn {
    font-size: 0.7rem;
  }
}
